.login-page{
    #navbar-content,
    .breadcrumb,
    .user-details{
        display: none !important;
    }
    form{
        background-color: #fff;
        border-radius: 10px;
        max-width: 50%;
        margin: auto auto 5em;
        padding: 1em .5em;
        input+label{
            margin-top: 10px;
        }
    }
    input[type=text], input[type=password] {
      width: 100%;
      padding: 12px 20px;
      margin: 8px 0;
      display: inline-block;
      border: 1px solid #ccc;
      box-sizing: border-box;
    }
    button {
        background-color: #4CAF50;
        color: white;
        padding: 14px 20px;
        margin: 8px 0;
        border: none;
        cursor: pointer;
        width: 100%;
        &:hover {
            opacity: 0.8;
        }
    }
    .cancelbtn {
        width: auto;
        padding: 10px 18px;
        background-color: #f44336;
    }
    .imgcontainer {
        text-align: center;
        margin: 24px 0 12px 0;
    }
    img.avatar {
        width: 40%;
        border-radius: 50%;
    }
    .container {
        padding: 16px;
    }
      
      /* The "Forgot password" text */
    span.psw {
        float: right;
        padding-top: 16px;
    }
    @media screen and (max-width: 300px) {
        span.psw {
            display: block;
            float: none;
        }
        .cancelbtn {
            width: 100%;
        }
    }
}
